body {
  width: 100%;
  overflow-x: hidden;
}

.App {
  text-align: center;
  background-color: #1f2229;
  width: 100%;
  overflow-x: hidden;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.class-icon {
  height: 80%;
  width: 80%;
}

@media screen and (max-width: 576px) {
  .class-icon {
    height: 100%;
    width: 100%;
  }
  a.info {
    margin: 35% 0 0 !important;
  }
  .playable-class-name {
    font-size: large;
  }
}

.navbar {
  background: rgba(255, 255, 255, 0.3);
  border-bottom: 2px solid rgba(225, 225, 225, 0.5);
  -webkit-filter: blur(0.5px), opacity(50%);
  filter: blur(0.5px), opacity(50%);
}

.navbar-collapse ul li a {
  transition: background 0.4s, text-shadow 0.4s;
  border-radius: 5px;
}

.navbar-collapse ul li a:hover {
  text-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
}

.Hunter:hover {
  background: rgba(171, 212, 115, 0.5);
  border: 1px solid rgb(171, 212, 115);
}

.Shaman:hover {
  background: rgba(0, 112, 222, 0.5);
  border: 1px solid rgb(0, 112, 222);
}

.Warlock:hover {
  background: rgba(148, 130, 201, 0.5);
  border: 1px solid rgb(148, 130, 201);
}

.Warrior:hover {
  background: rgba(199, 156, 110, 0.5);
  border: 1px solid rgb(199, 156, 110);
}

.Rogue:hover {
  background: rgba(255, 245, 105, 0.5);
  border: 1px solid rgb(255, 245, 105);
}

.Priest:hover {
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid rgb(255, 255, 255);
}

.Paladin:hover {
  background: rgba(245, 140, 186, 0.5);
  border: 1px solid rgb(245, 140, 186);
}

.Mage:hover {
  background: rgba(105, 204, 240, 0.5);
  border: 1px solid rgb(105, 204, 240);
}

.Druid:hover {
  background: rgba(255, 125, 10, 0.5);
  border: 1px solid rgb(255, 125, 10);
}

.nav-link {
  -webkit-text-stroke: 0.4px black;
}

.footer {
  -webkit-text-stroke: 0.8px black;
  font-weight: bold;
}

.footerWrapper {
  border-top: 1.4px solid white;
}

.navbar-brand {
  -webkit-text-stroke: 1px black;
}

.classIcon {
  border-radius: 25px;
}

.talent-frame {
  border: 2px solid white;
  color: white;
  border-radius: 4px;
  min-width: 305px;
  max-width: 305px;
}

.frame-wrappe {
  position: relative;
}

.talent-row {
  display: flex;
}

.talent-row-inner {
  position: relative;
}

.talentButton {
  margin-top: 0.7rem;
  margin-bottom: 0.7rem;
  margin-left: 0.15rem;
  margin-right: 0.15rem;
  border-radius: 3px;
  display: inline-block;
  z-index: 0;
  position: relative;
}

.talentHover {
  z-index: 1;
  position: absolute;
  top: 20%;
  left: 25%;
  width: 51%;
  height: 62%;
}

.talent-bg {
  background-repeat: no-repeat;
  background-size: cover;
}

.talentPoints {
  background-color: #000;
  border: #333;
  border-radius: 2px !important;
  color: #ffffff;
  cursor: pointer;
  font-size: 0.7rem;
  position: absolute;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  left: 60%;
  bottom: 5%;
  z-index: 2;
}

.angleArrow {
  position: absolute;
  z-index: 1;
  width: 3.95rem;
  height: 3.4rem;
  top: 45%;
  left: 76%;
}

.leftArrow {
  position: absolute;
  top: 40%;
  right: 75%;
  z-index: 1;
}

.rightArrow {
  position: absolute;
  top: 40%;
  left: 75%;
  z-index: 1;
}

.smArrow {
  position: absolute;
  left: 42%;
  top: 85%;
  height: 1.9rem;
  width: 0.8rem;
  z-index: 1;
}

.medArrow {
  position: absolute;
  left: 42%;
  top: 85%;
  height: 5.8rem;
  width: 0.8rem;
  z-index: 1;
}

.lgArrow {
  position: absolute;
  left: 43%;
  top: 85%;
  height: 13.65rem;
  width: 0.8rem;
  z-index: 1;
}

.lgArrowPaladin {
  position: absolute;
  left: 43%;
  top: 85%;
  height: 9.6rem;
  width: 0.8rem;
  z-index: 1;
}

.active-talent {
  border: 2px solid rgba(64, 191, 64, 0.8);
  border-radius: 3px;
}

.inactive-talent {
  border: 2px solid #c2c4c6;
  border-radius: 3px;
}

.maxeds {
  border: 2px solid #ffd100;
  border-radius: 3px;
}

.req-inactive {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

.req-active {
  -webkit-filter: grayscale(0%);
  filter: grayscale(0%);
}

.hovereffect {
  width: 100%;
  height: 100%;
  float: left;
  overflow: hidden;
  position: relative;
  text-align: center;
  cursor: default;
}

.overlay {
  width: 100%;
}

.hovereffect:hover {
  border-radius: 15px;
}

div[data-theme="Hunter"]:hover {
  box-shadow: 0 0 0 0.15rem #abd473;
}

div[data-theme="Shaman"]:hover {
  box-shadow: 0 0 0 0.15rem #0070de;
}

div[data-theme="Warlock"]:hover {
  box-shadow: 0 0 0 0.15rem #9482c9;
}

div[data-theme="Warrior"]:hover {
  box-shadow: 0 0 0 0.15rem #c79c6e;
}

div[data-theme="Rogue"]:hover {
  box-shadow: 0 0 0 0.15rem #fff569;
}

div[data-theme="Priest"]:hover {
  box-shadow: 0 0 0 0.15rem #ffffff;
}

div[data-theme="Paladin"]:hover {
  box-shadow: 0 0 0 0.15rem #f58cba;
}

div[data-theme="Mage"]:hover {
  box-shadow: 0 0 0 0.15rem #69ccf0;
}

div[data-theme="Druid"]:hover {
  box-shadow: 0 0 0 0.15rem #ff7d0a;
}

.hovereffect .overlay {
  height: 100%;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  transition: all 0.4s ease-in-out;
}

.hovereffect img {
  display: block;
  position: relative;
  transition: all 0.4s linear;
}

.hovereffect h2 {
  text-transform: uppercase;
  text-align: center;
  position: relative;
  font-size: 17px;
  background: rgba(0, 0, 0, 0.6);
  transform: translatey(-100px);
  transition: all 0.2s ease-in-out;
  padding: 10px;
}

.hovereffect a.info {
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  color: #fff;
  border: 1px solid #fff;
  background-color: transparent;
  opacity: 0;
  filter: alpha(opacity=0);
  transition: all 0.2s ease-in-out;
  margin: 45% 0 0;
  padding: 7px 14px;
}

.hovereffect a.info:hover {
  box-shadow: 0 0 5px #fff;
}

.hovereffect:hover img {
  transform: scale(0.75);
}

.hovereffect:hover .overlay {
  opacity: 1;
  filter: alpha(opacity=100);
}

.hovereffect:hover h2,
.hovereffect:hover a.info {
  opacity: 1;
  filter: alpha(opacity=100);
  transform: translatey(0);
}

.hovereffect:hover a.info {
  transition-delay: 0.2s;
}
